* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #0B2434;
  padding: 20px;
}

main {
    position: relative;
    background-color: #F5F5F5;
    height: 450px;
    max-width: 800px;
    border-radius: 5px;
    margin: 40px auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.title {
  margin-bottom: 1px;
}
.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
}

.dice-face {
  display: grid;
  
  /* grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr; */

  grid-template: repeat(3, 1fr) / repeat(3, 1fr); 
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0,0,0,0.15);
  border-radius: 10%;
  cursor: pointer;
  
  grid-template-areas:
		"a . c"
		"e g f"
		"d . b";
   background-color: #e7e7e7;
}
.die-num {
  font-size: 1.5rem;
  margin:13px 18px 18px 18px;
}
button {
   background-color: #5035ff;
   height: 50px;
   width: 100px;
   border: none;
   font-size: 1.2rem;
   color: white;
   border-radius: 6px;
 
   cursor: pointer;
}
button:focus {
  outline: none;
}
button:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0,0,0,0.7)
}
.pip {
  display: block;
	align-self: center;
	justify-self: center;
 	width: 11px;
	height: 11px;
	border-radius: 50%;
	background-color: rgba(114, 37, 208, 0.273);
  box-shadow: inset 0 3px rgb(172, 142, 227), inset 0 -3px rgb(230, 184, 230); 
}
.pip:nth-child(2) {
	grid-area: b;
}
.pip:nth-child(3) {
	grid-area: c;
}
.pip:nth-child(4) {
	grid-area: d;
}
.pip:nth-child(5) {
	grid-area: e;
}
.pip:nth-child(6) {
	grid-area: f;
}
/* This selects the last pip of odd-valued dice (1, 3, 5) and positions the pip in the center */
.pip:nth-child(odd):last-child {
	grid-area: g;
}
.toggle-cont {
   display: flex;
   width: 140px;
   justify-content: space-between;
   transition: background 0.2s linear;
   margin-bottom: 15px;
}
span {
  color:#ff35b8;
  font-weight: bold;
}
.label {
  width: 35px;
  height: 16px;
  background-color: #6ae23ade;
  border-radius: 50px;
  padding: 5px;
  transform: scale(1.5);
  margin:3px;
}
.ball {
  width:13px;
  height: 13px;
  background-color: #5035ff;
  position: absolute;
  top:1px;
  left:2px;
  border-radius: 50%;
}
.ball.clicked {
  transform: translate(18px);
}
.lost {
  position: absolute;
  background-color: #11c4a6e3;
  height: 250px;
  width: 500px;
  border-radius: 5px;
  margin: 40px auto;
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  top:60px;
  left:150px;
}
h1 {
  font-size: 3rem;
  text-transform: uppercase;
  color: #5035ff;
  animation-name: spin;
  animation-delay: 0s;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
  
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

